.chatapp  {
  margin: 0 auto;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  padding-bottom: 200px;
  /* padding-bottom: 100px; */
}
.header  {
  padding: 5px;
  color: white;
  margin-top: 0;
  text-align: left;
  border-bottom: 1px solid #3f51b5;
  background-color: #3f51b5;
  position: absolute;
  width: 339px
}
.message-list  {
  list-style-type: none;
  padding: 0px 10px;
  overflow-y: scroll;
  overflow-x: hidden;
  max-height: 325px;
  /* max-height: calc(100vh - 290px); */
  margin-bottom: 35px;
  text-align: left;
}
.message-sender  {
  font-weight: bold;
  font-size: 1em;
  margin: 0;
}
.message-text  {
  margin-top: 5px;
  margin-bottom: 5px
}
.message-input  {
  width: 70%;
  height: 50px;
  border: 0;
  border-top: 1px solid #e0e0e0;
  font-size: 18px;
  padding-left: 2px
}
.message-submit  {
  width: 30%;
  background-color: #3f51b5;
  color: white;
  font-size: 16px;
  cursor: pointer;
}
.input-field {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  margin-top: 30px;
  height: 40px;
}
